@use "sass:color";

$colorPrimary: (
    "regular": #a97140,
    "dark": color.scale(#a97140, $blackness: 50%),
    "light": color.scale(#a97140, $lightness: 50%)
);
$colorText: (
    "regular": #000000,
    "light": #928787,
    "lighter": color.scale(#000000, $lightness: 80%)
);
$colorBgCard: #222222;
$colorLine: #333131;
$colorBgTextArea: #f7f7f7;

$marginSection: 80px;

$fontTitle: 25px;
$textBold: (
    "bold": 600,
    "bolder": 700,
    "boldest": 800
);

$logoWidth: 65px;
$logoHeight: 30px;