@use 'config/_config' as *;
@use "sass:map";
@use "sass:math";

* {
    box-sizing: border-box;    
}

body {
    color: map.get($colorText, "light");
}

.section {
    margin-bottom: $marginSection;
}

.section-logo {
    &__navbar {
        filter: invert(1);
        width: 195px;
    }

    &__container {
        margin-bottom: 90px;
        left: 50%;
        transform: translateX(-50%);
        @include spaceImage($maxWidth: 195px, $imgWidth: 500, $imgHeight: 139);

        &::after {
            content: "";
            position: absolute;
            bottom: -20px;
            width: $logoWidth;
            height: $logoHeight;
            transform: translate(-50%, 100%);
            background-image: url(../../public/images/beard_icon.png);
            @include bgImg($repeat: no-repeat, $size: contain);
        }

        @media(max-width: 575px) {
            &::after {
                bottom: -10px;
                width: 60px;
                height: 30px;
            }
        }
    }

    &__image {
        filter: invert(1);
        @include imgToFillSpace;
    }
}

.section-title {
    display: inline-block;
    position: relative;
    // margin-bottom: 90px;
    margin-bottom: 75px;
    text-transform: uppercase;
    font-size: 20px;
    color: map.get($colorText, "regular");

    &--banner {
        @extend .section-title;
        margin-bottom: 50px;
        color: #ffffff;
    }

    &::after {
        content: "";
        position: absolute;
        top: 150%;
        left: 50%;
        width: $logoWidth;
        height: $logoHeight;
        transform: translateX(-50%);
        background-image: url(../../public/images/beard_icon.png);
        @include bgImg($repeat: no-repeat, $size: contain);
    }

    @media(max-width: 575px) {
        &::after {
            bottom: -10px;
            width: 60px;
            height: 30px;
        }
    }

    &--popular {
        @extend .section-title;
        color: #ffffff;
    }
}

.section-description {
    color: map.get($colorText, "regular");
    font-weight: 400;
}

.section-card {
    background-color: $colorBgCard;
    color: #ffffff;
    border: #ffffff;

    &--we-do {
        @extend .section-card;
        margin-bottom: 15px;
        background-color: transparent;
    }

    &--service {
        @extend .section-card;
        margin-bottom: 15px;
        background-color: transparent;
    }

    &__image {
        margin-bottom: 10px;
        @include spaceImage($maxWidth: 100%, $imgWidth: 5760, $imgHeight: 3840);        
    }

    &__price {
        display: inline-block;
        position: absolute;
        padding: 8px;
        top: 90%;
        left: 50%;
        transform: translateX(-50%);
        border: 1px solid #ffffff;
        background-color: map.get($colorPrimary, "regular");
        font-size: 20px;
    }

    &__title {
        padding-bottom: 15px;
        border-bottom: 1px solid $colorLine;
    }

    @media(min-width: 768px) and (max-width: 991px) {
        &__image {
            margin-bottom: 5px;
        }

        &__price {
            font-size: 15px;
        }

        &__title {
            font-size: 20px;
        }
    }

    @media(max-width: 767px) {
        &__image {
            @include spaceImage($maxWidth: 496px, $imgWidth: 5760, $imgHeight: 3840);
        }

        &--we-do {
            margin-bottom: 15px;
        }
    }

    @media(min-width: 426px) and (max-width: 767px) {
        &__price {
            top: 93%;
        }
    }
}

.section-button {
    margin-top: 50px;
    padding: 10px 25px;
    border-radius: 0;
    text-transform: uppercase;
    background-color: map.get($colorPrimary, "regular");
    color: #ffffff;

    &--contact {
        @extend .section-button;
        margin: 20px 0;
    }
}

.section-navbar {
    position: relative;
    background-color: $colorBgCard;

    &::before {
        content: "";
        position: absolute;
        top: 8px;
        right: 0;
        left: 0;
        border-top: 1px solid $colorLine;
    }    
    
    &::after {
        content: "";
        position: absolute;
        bottom: 8px;
        right: 0;
        left: 0;
        border-top: 1px solid $colorLine;
    }

    @media(max-width: 991px) {
        .navbar-nav {
            align-items: flex-start;
        }
    }
}

.nav-item--custom {
    position: relative;
    padding: 0 12px;
    font-weight: 600;
    text-align: center;
}

.nav-link--custom.active:hover {
    border-color: gray;
}

.nav-link--custom.active::before {
    content: "";
    position: absolute;
    top: -11px;
    right: 0;
    left: 0;
    margin: 0 2px;
    border-top: 2px solid map.get($colorPrimary, "regular");
    color: #ffffff;

    @media(max-width: 991px) {
        top: 0px;
        margin: 0 5px;
    }
}

.section-sosmed {
    margin: 0 10px;
    font-size: 20px;

    &--footer-icon {
        @extend .section-sosmed;
        color: map.get($colorText, "light");        
    }

    &--barber-icon {
        @extend .section-sosmed;
        margin: 0 10px;
        padding: 10px;
        color: #ffffff;
        background-color: $colorBgCard;
    }

    &:hover {
        color: map.get($colorPrimary, "regular");
        text-decoration: none;  
    }

    @media(max-width: 425px) {
        font-size: 14px;

        &--barber-icon {
            font-size: 12px;
        }
    }
}

.section-footer {
    display: block;
    position: relative;
    z-index: 1;
    padding: 50px;
    min-height: 300px;
    @include bgImg();
    background-image: url("../../public/images/beard_footer.jpg");
    color: map.get($colorPrimary, "regular");
    font-size: 12px;
    font-weight: 700;

    &::before {
        z-index: -1;
        @include bgTransparent($isWhite: 0, $transparent: 0.7)
    }

    &__contact {
        color: #ffffff;
        font-size: 28px;
    }

    @media(max-width: 425px) {
        &__contact {
            font-size: 20px;
        }
    }
}

.section-botbar {
    padding: 30px;
    font-size: 14px;
    font-weight: 600;
    color: map.get($colorText, "light");
    background-color: #000000;

    @media(max-width: 425px) {
        font-size: 12px;
    }
}